define('dummy/controllers/index', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  /* jshint newcap: false */

  var observer = Ember['default'].observer;
  var computed = Ember['default'].computed;
  var on = Ember['default'].on;

  exports['default'] = Ember['default'].Controller.extend({

    abc: [1],

    fullName: computed('firstName', 'lastName', {
      get: function get() {
        return this.get('firstName') + ' ' + this.get('lastName');
      },
      set: function set(key, value) {
        var parts = value.split(/\s+/);
        var firstName = parts[0];
        var lastName = parts[1];
        this.set('firstName', firstName);
        this.set('lastName', lastName);
      }
    }),

    some: Ember['default'].inject.controller('some'),
    service: Ember['default'].inject.service('someService'),

    a: Ember['default'].A([1, 2, 3]),
    b: Ember['default'].A([1, 2, 3]),

    firstName: 'a',
    lastName: 'b',

    notComputedEmberObject: Ember['default'].Object.create({
      a: 1,
      b: 'b',
      c: [1, 2, 3],
      d: true
    }),

    notComputedObject: {
      a: 'a',
      b: 123,
      c: true,
      d: null
    },

    computedPropertyEmberObject: computed('a.firstObject', 'b.[]', function () {
      return Ember['default'].Object.create({
        a: this.get('a.firstObject'),
        b: this.get('b.length')
      });
    }),

    computedPropertyWithGetterSetter: computed('firstName', 'lastName', {
      get: function get() {
        return this.get('firstName') + ' ' + this.get('lastName');
      },
      set: function set(key, value) {
        var parts = value.split(/\s+/);
        this.setProperties({
          firstName: parts[0],
          lastName: parts[1]
        });
      }
    }),

    method: function method(a, b, c) {
      this.set('c', a + b + c);
    },

    methodObserver: observer('a.[]', 'b.[]', function () {
      var a = this.get('a.length');
      var b = this.get('b.length');
      this.set('c', a + b);
    }),

    methodListener: on('init', function () {
      var a = this.get('a.length');
      var b = this.get('b.length');
      this.set('c', a + b);
    }),

    methodObserverListener: on('init', observer('a.[]', 'b.[]', function () {
      var a = this.get('a.length');
      var b = this.get('b.length');
      this.set('c', a + b);
    })),

    actions: {

      someAction: function someAction() {
        var a = this.get('a');
        this.set('c', a.get('length'));
      }

    }

  });

});