define('dummy/controllers/some', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var observer = Ember['default'].observer;
  var computed = Ember['default'].computed;

  exports['default'] = Ember['default'].Controller.extend({

    someVariable1: computed('abc.[]', 'def.length', function () {
      return this.get('abc.length');
    }),

    someFunction1: observer('abc', 'def', 'ghi.[]', function () {
      var abc = this.get('abc');
      this.set('def', abc);
    }),

    actions: {
      someAction1: function someAction1() {
        this.set('abc', 123);
      }
    }

  });

});