define('dummy/tests/unit/helpers/to-string-test', ['dummy/helpers/to-string', 'qunit', 'ember'], function (to_string, qunit, Ember) {

  'use strict';

  function minifier(str) {
    return str.replace(/\s+/g, ' ');
  }

  var targetObject = Ember['default'].Controller.extend({

    a: Ember['default'].A([1, 2, 3]),
    b: Ember['default'].A([1, 2, 3]),

    firstName: 'a',
    lastName: 'b',

    notComputedEmberObject: Ember['default'].Object.create({
      a: 1,
      b: 'b',
      c: [1, 2, 3],
      d: true
    }),

    notComputedObject: {
      a: 'a',
      b: 123,
      c: true,
      d: null
    },

    computedPropertyEmberObject: Ember['default'].computed('a.firstObject', 'b.[]', function () {
      return Ember['default'].Object.create({
        a: this.get('a.firstObject'),
        b: this.get('b.length')
      });
    }),

    computedPropertyWithGetterSetter: Ember['default'].computed('firstName', 'lastName', {
      get: function get() {
        return this.get('firstName') + ' ' + this.get('lastName');
      },
      set: function set(key, value) {
        var parts = value.split(/\s+/);
        this.setProperties({
          firstName: parts[0],
          lastName: parts[1]
        });
      }
    }),

    method: function method(a, b, c) {
      this.set('c', a + b + c);
    },

    methodObserver: Ember['default'].observer('a.[]', 'b.[]', function () {
      var a = this.get('a.length');
      var b = this.get('b.length');
      this.set('c', a + b);
    }),

    methodListener: Ember['default'].on('init', function () {
      var a = this.get('a.length');
      var b = this.get('b.length');
      this.set('c', a + b);
    }),

    methodObserverListener: Ember['default'].on('init', Ember['default'].observer('a.[]', 'b.[]', function () {
      var a = this.get('a.length');
      var b = this.get('b.length');
      this.set('c', a + b);
    })),

    service: Ember['default'].inject.service('someService'),

    // dummy way to simulate container
    container: {
      lookup: Ember['default'].K
    },

    actions: {

      someAction: function someAction() {
        var a = this.get('a');
        this.set('c', a.get('length'));
      }

    }

  }).create();

  qunit.module('Unit | Helper | to string');

  qunit.test('Not computed property. Ember Object', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'notComputedEmberObject'])), 'notComputedEmberObject: Ember.Object.create({ "a": 1, "b": "b", "c": [ 1, 2, 3 ], "d": true })');
  });

  qunit.test('Not computed property. Plain Object', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'notComputedObject'])), 'notComputedObject: { "a": "a", "b": 123, "c": true, "d": null }');
  });

  qunit.test('Computed property. Ember Object. Calculated', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'computedPropertyEmberObject', true])), 'computedPropertyEmberObject: Ember.Object.create({ "a": 1, "b": 3 })');
  });

  qunit.test('Computed property. Ember Object. Raw', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'computedPropertyEmberObject'])), 'computedPropertyEmberObject: Ember.computed(\'a.firstObject\', \'b.[]\', function () { return Ember.Object.create({ a: this.get(\'a.firstObject\'), b: this.get(\'b.length\') }); })');
  });

  qunit.test('Computed property with getter and setter', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'computedPropertyWithGetterSetter'])), 'computedPropertyWithGetterSetter: Ember.computed(\'firstName\', \'lastName\', { function get() { return this.get(\'firstName\') + \' \' + this.get(\'lastName\'); }, function set(key, value) { var parts = value.split(/\\s+/); this.setProperties({ firstName: parts[0], lastName: parts[1] }); } })');
  });

  qunit.test('Action', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'someAction'])), 'function someAction() { var a = this.get(\'a\'); this.set(\'c\', a.get(\'length\')); }');
  });

  qunit.test('Method', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'method'])), 'method: function method(a, b, c) { this.set(\'c\', a + b + c); }');
  });

  qunit.test('Method is observer', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'methodObserver'])), 'methodObserver: Ember.observer(\'a.[]\', \'b.[]\', function () { var a = this.get(\'a.length\'); var b = this.get(\'b.length\'); this.set(\'c\', a + b); })');
  });

  qunit.test('Method is listener', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'methodListener'])), 'methodListener: Ember.on(\'init\', function () { var a = this.get(\'a.length\'); var b = this.get(\'b.length\'); this.set(\'c\', a + b); })');
  });

  qunit.test('Method is observer and listener', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'methodObserverListener'])), 'methodObserverListener: Ember.on(\'init\', Ember.observer(\'a.[]\', \'b.[]\', function () { var a = this.get(\'a.length\'); var b = this.get(\'b.length\'); this.set(\'c\', a + b); }))');
  });

  qunit.test('Injection', function (assert) {
    assert.equal(minifier(to_string.toString([targetObject, 'service'])), 'service: Ember.inject.service(\'someService\')');
  });

});